/* eslint-disable no-unused-vars */
import React, { Component } from "react"
import MetaTags from 'react-meta-tags';

import { MDBDataTable } from "mdbreact"


import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"
// import Select from "react-select"
// import axios from 'axios'
// import  { Redirect } from 'react-router-dom'
// // import $ from "jquery";

// import Flatpickr from "react-flatpickr"
// import ReactSelect from 'react-select'
// import makeAnimated from 'react-select/animated';
//import BootstrapSelect from "bootstrap-select"
// import conf from '../../../conf.js'

// import Confirmer from '../../../components/Confirmer.js'
import conf from '../../conf.js';
import ModalDetailTerrain from '../visiteur/ModalDetailTerrain'
import $ from 'jquery'
import  { Redirect } from 'react-router-dom'

class ListeDesReservations extends Component {


  constructor(props) {
    super(props)
    this.state = {
      users:[],
      userData:[],
      id:0,
      show:false,
      ok:"",
      reservations:[],
      detailModal: false,
      periode:"jj;ee"
    }
    this.handleTerrainClick.bind(this)
  }
  componentDidMount() {
    let  data=  JSON.parse(localStorage.getItem('token_auth'))
    $("#chargement").css("display","none")
   
   const requestOptions = {
    headers: { 
      'Authorization':'Bearer '+data.token,

    },

    'method':'GET'
   };

   fetch(conf.url+"user/users/"+data.user.id,requestOptions)
   .then((response) => response.json())
   .then((data) => {
     console.log("data",data)
    try{
      
      if(data.terrains.length){
        console.log("data2",data)
       
        data.terrains.map((val,idx)=>{
          console.log("terrain <<",val)
          val.reservations.filter((filtre)=>filtre.statePayement=="PAYE").map((element,idx)=>{
            console.log("reservation>>",element)
            var nom=""
            if(element.userDoReservation){
              nom=element.userDoReservation.nom+" "+element.userDoReservation.prenom;
            }
            
         var tab= {
           id: element.id,
           nomterrain: element.nomTerrain,
           datedereservation: element.dateReservation,
           periode: <span onClick={this.handleTerrainClick.bind(this,element.heure)}> <i style={{cursor:"pointer"}}  className="bx bx-time-five bx-sm text-success "/>  </span>,
           nomclient: nom,
           adresse: element.userDoReservation?.adresse,
           telephone:element.userDoReservation?.telephone,
           total: element.montantTotal+ " F",
        
           action:  <a> <i style={{cursor:"pointer"}}  className="bx bx-info-circle bx-sm text-primary "/>  </a>,
           
          }
         //onClick={this.handleDetail.bind(this.bind,element.id)}
         console.log("okkk")
         this.setState({reservations:[...this.state.reservations,tab]})
          })
        })
      }else{
        this.setState({reservations:[]})
      }
    }catch(error){
      this.setState({reservations:[]})
    }
      

     })
  };

  toggle() {
    this.setState(prevState => ({
      modal: !prevState.modal,
    }))
  }

  handleTerrainClick = (data) => {
  
    this.setState({detailModal:true,periode:data})
    
    this.toggle()
  }

  render() {

    const data = {
      columns: [
        {
          label: "Térrain",
          field: "nomterrain",
          sort: "asc",
          width: 270,
        },
        {
          label: "Date",
          field: "datedereservation",
          sort: "asc",
          width: 270,
        },
        {
          label: "Périodes",
          field: "periode",
          sort: "asc",
          width: 100,
        },
        {
          label: "Client",
          field: "nomclient",
          sort: "asc",
          width: 100,
        },
        {
          label: "Adr Client",
          field: "adresse",
          sort: "asc",
          width: 100,
        },
        {
          label: "Tél Client",
          field: "telephone",
          sort: "asc",
          width: 100,
        },
        {
          label: "Total",
          field: "total",
          sort: "asc",
          width: 100,
        },
        {
          label: "Action",
          field: "action",
          sort: "asc",
          width: 100,
        },
      ],
      rows:this.state.reservations
      
    }


    return (
      <React.Fragment>
        <div className="page-content"> 
          {this.state.ok}
        {/* <Confirmer show={this.state.show} /> */}

        <ModalDetailTerrain
                show={this.state.detailModal}
                terrain={this.state.periode}
                type="RESERVATION"
                onCloseClick={() => this.setState({ detailModal: false })}
             />

          <Container fluid={true}>
            
            <Row>
              <Col md="12" className="pull-right">
                <Card>
                  <CardBody>
                    <Row ClassName="mb-4">
                      <Col md="8">
                        <div className="page-title-box d-flex align-items-center justify-content-between">
                          <h1 className="mb-0 font-size-18">Liste des réservations</h1>
                        </div>
                      </Col>
                      <Col md="4" className="pull-right">
                        <Button
                          color="success"
                          className="font-16 btn btn-success float-right"
                          href="/#/indisponibilite"
                        >
                          <i class='bx bx-plus-circle' > </i>  Renseigner une réservation
                        </Button>
                      </Col>
                    </Row>

                    <hr/>

                    <Row ClassName="mt-5">
                      <Col md="12">
                        <MDBDataTable responsive striped bordered data={data} />
                      </Col>
                    </Row>

                  </CardBody>

                </Card>
              </Col>
            </Row>
              
            
          </Container>
        </div>
        
      </React.Fragment>
    )
  }
}
export default ListeDesReservations