import React, { Component } from "react"
import PropTypes from 'prop-types'
import { Col, Modal, ModalBody, Row } from "reactstrap"
import { format } from 'date-fns';
import  { Redirect } from 'react-router-dom'

  class ModalDetailTerrain extends Component {
    constructor(props) {
      super(props)
    
      this.state = {
        ok:"",
        prix:{}

      }
    }

    componentDidMount() {
      console.log("modea",   this.props.terrain)
      // this.props.terrain.prix.map((val)=>{
      //   this.state.prix[""+val.jour.toLowerCase()]=val.valeur
      //   this.setState({prix:this.state.prix})
      // })
    }




  render (){
  const show =this.props.show
  const onCloseClick=this.props.onCloseClick
  const terrain=this.props.terrain
  const type=this.props.type

  return (
    <Modal isOpen={show} toggle={onCloseClick} centered={true}>
      <ModalBody className="py-3 px-5">     {this.state.ok}
        <Row className="mb-3">
          <Col lg={12}>
            <div >
              <i className="bx bx-football text-success display-5" style={{ lineHeight : '1'}}/>
          
            </div>
          </Col>
        </Row>
        <Row className="mb-3">

          <Col lg={12}>
            {
            (type==="TERRAIN")?
            <div>
              <Row>
                <Col>
                <br/>
                <br/>
                  <div className="mb-2"><strong> Nom:      </strong> <span>{terrain.nom}</span></div>
                  <div className="mb-2"><strong> Adresse:         </strong><span>{terrain.adresse}</span></div>
                  <div className="mb-2"><strong> Ville:         </strong><span>{terrain.ville}</span></div>
                  {/* <div className="mb-2"><strong> Prix Heure :         </strong><span>{terrain.prixHeure} FCFA</span></div>
                  <div className="mb-2"><strong> Prix Démi-Heure : </strong><span>{terrain.prixDemiHeure} FCFA</span></div> */}
                </Col>
                {/* <div style="border-left:1px solid #000;height:500px"></div> */}
                <Col>
                  <div className="mb-2 text-center"><strong> Les différents prix :    </strong></div>
                 { 
                   (terrain.prix)?
                  
                   (terrain.prix.map((val)=>(
                    (<div className="mb-2 text-center"><strong>{val.jour} :         </strong><span>{val.valeur} FCFA</span></div>)
                   )) 
                   ):null 
                
                 }
                  
                  
                </Col>
              </Row>
            </div>:null
            }

          {
            (type==="RESERVATION")?
            <div>
                <ul class="ks-cboxtags">
                            {
                              terrain.split(";").map((element,idx)=>(
                                (element!="")?
                                <li className="mr-2"><input type="checkbox"  id={"checkbox"+idx} checked value={element} /><label for={"checkbox"+idx}>{element}</label></li>:null
                                
                              ))
                            }
                   
                 </ul>
            </div>:null
          }
          </Col>
          
        </Row>

        <Row>
          <Col>
            <div className="mt-3" style={{float:"right"}}>
              
              <button
                type="button"
                className="btn btn-success btn-sm me-2"
                onClick={onCloseClick}
              >
                Fermer
              </button>
            </div>
          </Col>
        </Row>
        
      </ModalBody>
    </Modal>
  )
}


  }
export default ModalDetailTerrain
